import { Trans } from '@lingui/macro'
import { AccessTime } from '@material-ui/icons'
import React from 'react'
import styled from 'styled-components'

import { IconLine } from '../../../atoms/IconLine'
import { formatTimeWithoutTodayDate } from '../../../helpers/formats'
import { Colors } from '../../../styling'

type TProps = NoChildren & {
  deliverTime: Maybe<Date>
  deliverType: 'DELIVER_UNTIL' | 'DELIVER_AT_SPECIFIC_TIME' | 'PERSONAL'
}
const Label = styled.span`
  color: ${Colors.greyDark};
`
const DeliveryTime = styled.div`
  color: ${Colors.black};
  font-weight: bold;
  display: inline-block;
  margin-left: 0.3rem;
`

const DeliverLabel = ({ deliverType }) => {
  switch (deliverType) {
    case 'DELIVER_UNTIL':
      return (
        <Label>
          <Trans>Deliver until</Trans>
        </Label>
      )
    case 'DELIVER_AT_SPECIFIC_TIME':
      return (
        <Label>
          <Trans>Deliver at</Trans>
        </Label>
      )
    case 'PERSONAL':
      return (
        <Label>
          <Trans>Personal pickup at</Trans>
        </Label>
      )
    default:
      return null
  }
}

export const KitchenOrderAtSummary: React.FC<TProps> = ({
  deliverTime,
  deliverType,
}) => {
  return (
    <IconLine icon={AccessTime} labelMargin="0">
      <DeliverLabel deliverType={deliverType} />
      {deliverTime && (
        <DeliveryTime>{formatTimeWithoutTodayDate(deliverTime)}</DeliveryTime>
      )}
    </IconLine>
  )
}
