import React from 'react'
import styled, { css } from 'styled-components/macro'

import { Colors, FontSizes } from '../../../styling'
import { TouchCheckButton } from '../atoms/TouchCheckButton'
import { TKitchenCardItemCommon } from '../kitchen.types'

type TProps = NoChildren & {
  item: TKitchenCardItemCommon
  isChecked?: boolean
  canHandle?: boolean
  onHandle?(): void
  isFirstSidedish?: boolean
}

const firstSideDishStyle = css`
  margin-top: -0.8rem;
`

const StyledKitchenCardItem = styled.div`
  display: flex;
  justify-content: space-between;
  ${(props: { isFirstSidedish: boolean }) =>
    props.isFirstSidedish && firstSideDishStyle};

  & .done-container {
    flex: 0 0 2.4rem;
  }

  & .item-code {
    flex: 0 0 4.1rem;
    font-size: ${FontSizes.CompactCardBig};
    color: ${Colors.black};
    margin-top: 0.35rem;
  }

  & .item-name {
    flex: 1 0;
    color: ${Colors.greyMiddle};
    margin: 0.4rem 0 0 0.5rem;
  }
`

export const KitchenCardItem: React.FC<TProps> = ({
  item,
  isChecked = false,
  canHandle = false,
  onHandle,
  isFirstSidedish = false,
}) => (
  <StyledKitchenCardItem isFirstSidedish={isFirstSidedish}>
    <div className="done-container">
      {canHandle && onHandle && (
        <TouchCheckButton isChecked={isChecked} onClick={onHandle} />
      )}
    </div>
    <div className="item-code">{item.code}</div>
    <div className="item-name">{item.name}</div>
  </StyledKitchenCardItem>
)
