import React from 'react'
import styled from 'styled-components'

import { Colors, Spacing } from '../../../styling'

type TProps = NoChildren & {
  isChecked: boolean
  onClick(): void
}

const Circle = styled.div`
  background: ${Colors.white};
  border: 2px solid ${Colors.green};
  width: 2.1rem;
  height: 2.1rem;
  position: relative;
  border-radius: 50%;
  margin-bottom: ${Spacing.Small};
`
const CheckedInner = styled.div`
  background: ${Colors.green};
  border-radius: 50%;
  margin: 0;
  width: 70%;
  height: 70%;
  overflow: auto;
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
`

export const TouchCheckButton: React.FC<TProps> = ({ isChecked, onClick }) => (
  <Circle onClick={onClick}>{isChecked && <CheckedInner />}</Circle>
)
