import { Trans } from '@lingui/macro'
import { observer } from 'mobx-react-lite'
import React from 'react'

import { Mobile } from '../../../app/Responsive'
import { CardListTitle } from '../../../atoms/CardListTitle'
import { OrderNotificationSound } from '../../../molecules/OrderNotificationSound'
import { KitchenInfoMessage } from '../atoms/KitchenInfoMessage'
import { useKitchenHandler } from '../hooks/useKitchenHandler'
import { TKitchenCardState } from '../kitchen.types'
import { KitchenCardOrder } from '../molecules/KitchenCardOrder'
import { KitchenCardSector } from '../molecules/KitchenCardSector'
import { useKitchenDeck } from './KitchenLoader'

type TProps = NoChildren & {
  state: TKitchenCardState
  noCardsMessage: ReactNode
  mobileHeadline?: ReactNode
}

export const KitchenShelf = observer<TProps>(
  ({ state, noCardsMessage, mobileHeadline }) => {
    const shelfCards = useKitchenDeck(state)
    const handler = useKitchenHandler()

    if (shelfCards === null) {
      return (
        <KitchenInfoMessage>
          <Trans>Loading</Trans>
        </KitchenInfoMessage>
      )
    }

    if (shelfCards.length === 0) {
      return <KitchenInfoMessage>{noCardsMessage}</KitchenInfoMessage>
    }

    const shouldPlayNotification = state === 'todo'

    return (
      <>
        {mobileHeadline && (
          <Mobile>
            <CardListTitle>{mobileHeadline}</CardListTitle>
          </Mobile>
        )}
        <OrderNotificationSound shouldPlay={shouldPlayNotification} playNow />
        {shelfCards.map(card => {
          const commonProps = {
            key: `${card.type}-${card.id}`,
            canHandle: handler.canHandleCard(card),
            canHandleRecipe: handler.canHandleCardItem(card),
            onHandle: handler.handleCard,
            onHandleRecipe: handler.handleCardItem,
          }
          if (card.type === 'order') {
            return <KitchenCardOrder card={card} {...commonProps} />
          }
          if (card.type === 'sector') {
            return <KitchenCardSector card={card} {...commonProps} />
          }
          return null
        })}
      </>
    )
  },
)
