import './SectorsFilter.css'

import { useXField } from '@speedlo/xform'
import React from 'react'
import DropdownTreeSelect, {
  DropdownTreeSelectProps,
  TreeNode,
} from 'react-dropdown-tree-select'

import { useQFilterSectors } from '../../graph/generated'
import { ARRAY_EMPTY } from '../../helpers/form'
import { i18n } from '../../i18n'
import { idProp, RM } from '../../tools/ramda'
import { useBranchFilter } from './BranchFilter'

const DropdownTreeSelectMemo = React.memo<DropdownTreeSelectProps & NoChildren>(
  ({ texts, data, onChange }) => {
    // this wrapper is necessary because of the silly implementation of the component
    // which basically resets the state on each render
    return <DropdownTreeSelect texts={texts} data={data} onChange={onChange} />
  },
)

type TProps = NoChildren & {
  markAsAvailable?(isAvailable: boolean): void
}

export const SectorsFilter = React.memo<TProps>(({ markAsAvailable }) => {
  const branch = useBranchFilter()
  const { getStash } = useQFilterSectors({
    branch,
  })

  const sectors = getStash(data => data.sectors)

  const [treeData, setTreeData] = React.useState<RoA<TreeNode>>(ARRAY_EMPTY)
  const field = useXField<RoA<ID>>('sectors')

  const hasAnySectors = sectors !== null && sectors.length > 0

  React.useEffect(() => {
    if (sectors === null) {
      return
    }
    setTreeData(
      getTreeFromFlatData({
        flatData: sectors,
        mapper: sector => ({
          label: sector.name,
          value: sector.id,
        }),
      }),
    )
  }, [sectors])

  React.useEffect(() => {
    if (markAsAvailable) {
      markAsAvailable(hasAnySectors)
    }
  }, [hasAnySectors, markAsAvailable])

  const onChange = React.useCallback(
    (currentNode: TreeNode, selectedNodes: TreeNode[]) => {
      field.setValue(selectedNodes.map(RM.prop('value')))
    },
    [field],
  )

  const texts = React.useMemo(
    () => ({
      placeholder: i18n.t`Filter by sectors`,
      noMatches: i18n.t`No sectors`,
    }),
    [],
  )

  if (!hasAnySectors) {
    return null
  }

  return (
    <DropdownTreeSelectMemo texts={texts} data={treeData} onChange={onChange} />
  )
})

function getTreeFromFlatData<T extends Record<string, any>, R>({
  flatData,
  mapper,
  getKey = idProp,
  getParentKey = node => (node.parent ? node.parent.id : null),
}: {
  flatData: RoA<T>
  mapper(node: T): R
  getKey?(node: T): string
  getParentKey?(node: T): string | null
}): Array<T & R & { children: T[] }> {
  if (!flatData) {
    return []
  }

  const rootKey = '@@'

  const childrenToParents = { [rootKey]: [] as T[] }
  flatData.forEach(child => {
    const parentKey = getParentKey(child) || rootKey

    if (parentKey in childrenToParents) {
      childrenToParents[parentKey].push(child)
    } else {
      childrenToParents[parentKey] = [child]
    }
  })

  if (!(rootKey in childrenToParents)) {
    return []
  }

  const trav = (parent: T) => {
    const parentKey = getKey(parent)
    if (parentKey in childrenToParents) {
      return {
        ...parent,
        ...mapper(parent),
        children: childrenToParents[parentKey].map(child => trav(child)),
      }
    } else {
      return {
        ...parent,
        ...mapper(parent),
        children: ARRAY_EMPTY,
      }
    }
  }

  return childrenToParents[rootKey].map(child => trav(child))
}
