import {
  FKitchenCompletionOrderFragment,
  OrderStateCategoryEnum,
  QKitchenCompletionOrderListQuery,
  SKitchenCompletionOrderListSubscription,
  useQKitchenCompletionOrderList,
  useSKitchenCompletionOrderList,
} from '../../../graph/generated'
import { useOnConnectionRestored } from '../../../hooks/useOnConnectionRestored'
import { RM } from '../../../tools/ramda'
import { TKitchenDeckInput } from '../kitchen.types'

const requiredCategories = [
  OrderStateCategoryEnum.NewOrders,
  OrderStateCategoryEnum.Preparing,
  OrderStateCategoryEnum.ReadyToDeliver,
]

const sortForCompletion = RM.sortWith<FKitchenCompletionOrderFragment>([
  RM.descend(RM.prop('isPriority')),
  RM.ascend(RM.prop('orderNumber')),
  RM.ascend(RM.either(RM.prop('deliverAt'), RM.prop('createdAt'))),
])

export function useKitchenCompletionDeck({
  branch,
  sectors,
  occasions,
}: TKitchenDeckInput) {
  const skip = branch === null

  const variables = {
    branches: [branch!],
    occasions,
    sectors,
  }

  const { getStash, updateQuery, refetch } = useQKitchenCompletionOrderList(
    {
      ...variables,
      categories: requiredCategories,
    },
    { skip },
  )
  useOnConnectionRestored(refetch)

  useSKitchenCompletionOrderList(
    {
      ...variables,
      // subscription needs to watch for finished state to remove such orders from view
      categories: requiredCategories.concat(OrderStateCategoryEnum.Finished),
    },
    {
      skip,
      onSubscriptionData({ subscriptionData: { data: dataUpdate } }) {
        if (dataUpdate) {
          updateQuery(prev => handleSubscriptionUpdate(dataUpdate, prev))
        }
      },
    },
  )

  return getStash(data => sortForCompletion(data.orders))
}

function handleSubscriptionUpdate(
  update: SKitchenCompletionOrderListSubscription,
  prev: QKitchenCompletionOrderListQuery,
) {
  const { order } = update.updatedOrder
  if (!order || !prev.orders) {
    return prev
  }
  if (!requiredCategories.includes(order.orderStateCategory)) {
    return { orders: prev.orders.filter(o => o.id !== order.id) }
  }
  let orderExists = false
  const orders = prev.orders.map(o => {
    if (o.id === order.id) {
      orderExists = true
      return order
    }
    return o
  })
  if (!orderExists) {
    orders.unshift(order)
  }
  return { orders }
}
