import React from 'react'

import {
  QFilterOccasionsOccasions,
  useQFilterOccasions,
} from '../../graph/generated'
import { ARRAY_EMPTY } from '../../helpers/form'
import { i18n } from '../../i18n'
import { RM } from '../../tools/ramda'
import { FormDropdownMulti } from '../forms/FormDropdownMulti'
import { useBranchFilter } from './BranchFilter'

type TOccasion = QFilterOccasionsOccasions

type TProps = NoChildren & {
  markAsAvailable?(isAvailable: boolean): void
}

export const OccasionsFilter = React.memo<TProps>(({ markAsAvailable }) => {
  const branch = useBranchFilter()
  const { getStash, loading } = useQFilterOccasions({
    branch,
  })

  const occasions = getStash(data => data.occasions)
  const hasAnyOccasions = occasions !== null && occasions.length > 0

  React.useEffect(() => {
    if (markAsAvailable) {
      markAsAvailable(hasAnyOccasions)
    }
  }, [hasAnyOccasions, markAsAvailable])

  return (
    <FormDropdownMulti<TOccasion>
      fieldName="occasions"
      placeholder={i18n.t`Filter by occasions`}
      options={occasions || ARRAY_EMPTY}
      noOptionsMessage={() => i18n.t`No occasions`}
      isLoading={loading}
      getOptionLabel={RM.prop('title')}
      getOptionFilter={RM.prop('title')}
    />
  )
})
