import React from 'react'
import styled from 'styled-components/macro'

import { CardDock } from '../../../atoms/CardDock'
import { Colors, FontSizes, Radiuses, Spacing } from '../../../styling'
import { ToBranchContainerStyled } from '../../order/molecules/OrderTicker'
import { KitchenCardButton } from '../atoms/KitchenCardButton'
import { useKitchenHandler } from '../hooks/useKitchenHandler'
import { TKitchenCardCompletion } from '../kitchen.types'
import { KitchenCardItems } from './KitchenCardItems'
import { KitchenOrderStarIndicator } from './KitchenOrderStarIndicator'

type TProps = NoChildren & {
  card: TKitchenCardCompletion
  showBranch: boolean
}

export const StyledKitchenCardCompletion = styled.div`
  font-size: ${FontSizes.CompactCard};
  background-color: ${Colors.white};
  color: ${Colors.greyDarkest};
  border-radius: ${Radiuses.Card};
  margin-bottom: ${Spacing.Middle};
  padding-bottom: ${Spacing.Middle};
  position: relative;

  & .header {
    display: flex;
    flex-wrap: nowrap;
  }

  & .order__number {
    width: 4.5rem;
    background: ${Colors.orange};
    color: ${Colors.white};
    font-size: 2rem;
    border-top-left-radius: ${Radiuses.Card};
    text-align: center;
  }

  & .order__source {
    display: inline-block;
    background: ${Colors.greyMiddle};
    color: ${Colors.white};
    font-size: 0.9rem;
    padding: 0.1rem 0.8rem;
    border-radius: 0.9rem;
    margin: 0.5rem;
  }

  & .order__sector {
    display: inline-block;
    color: ${Colors.black};
    margin: 0.5rem;
  }

  & .order__content {
    margin: ${Spacing.Middle};
  }
`

export const KitchenCardCompletion: React.FC<TProps> = ({
  card,
  showBranch,
}) => {
  const handler = useKitchenHandler()
  return (
    <StyledKitchenCardCompletion data-testid="order-box">
      {showBranch && (
        <ToBranchContainerStyled>
          {card.companyBranch.name}
        </ToBranchContainerStyled>
      )}
      <div className="header">
        <div className="order__number">{card.orderNumber}</div>
        <div className="order__source">{`${card.orderOrigin.code} ${
          card.cashBox !== null ? card.cashBox.id : ''
        }`}</div>
        {card.sector && <div className="order__sector">{card.sector.name}</div>}
        {card.isPriority && <KitchenOrderStarIndicator />}
      </div>

      <div className="order__content">
        <KitchenCardItems
          items={card.items}
          canHandle={handler.canHandleCardItem(card)}
          onHandle={item => handler.handleCardItem(card, item)}
        />
        {card.note && <div className="order__note">{card.note}</div>}
      </div>

      {handler.canHandleCard(card) && (
        <CardDock position="BottomRight">
          <KitchenCardButton
            color={card.state === 'todo' ? Colors.green : Colors.red}
            onClick={() => handler.handleCard(card)}
          />
        </CardDock>
      )}
    </StyledKitchenCardCompletion>
  )
}
