import { Trans } from '@lingui/macro'
import React from 'react'
import styled from 'styled-components/macro'

import { CardDock } from '../../../atoms/CardDock'
import { HorizontalSeparator } from '../../../atoms/HorizontalSeparator'
import { LinkStyled } from '../../../atoms/LinkStyled'
import { DeliveryTypeEnum } from '../../../graph/generated'
import { formatAddress } from '../../../helpers/formats'
import { Colors, FontSizes, Radiuses, Spacing } from '../../../styling'
import { CustomerIdentity } from '../../order/atoms/CustomerIdentity'
import { DeliveryTimeBadge } from '../../order/atoms/DeliveryTimeBadge'
import { NoteLine } from '../../order/molecules/NoteLine'
import { KitchenCardButton } from '../atoms/KitchenCardButton'
import { KitchenOrderAtSummary } from '../atoms/KitchenOrderAtSummary'
import { TKitchenCardOrder, TKitchenCardProps } from '../kitchen.types'
import { KitchenCardItems } from './KitchenCardItems'
import { KitchenOrderStarIndicator } from './KitchenOrderStarIndicator'

type TProps = NoChildren &
  TKitchenCardProps<TKitchenCardOrder> & {
    card: TKitchenCardOrder
  }

const StyledKitchenCardOrder = styled.div`
  font-size: ${FontSizes.CompactCard};
  background-color: ${Colors.white};
  color: ${Colors.greyDarkest};
  border-radius: ${Radiuses.Card};
  margin-bottom: ${Spacing.Middle};
  position: relative;

  .order-ticker {
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    padding: 0.2rem;
    color: ${Colors.greyMiddle};
  }

  .details {
    padding: 0 0.2rem 0.1rem 0.2rem;
    position: relative;
  }

  .delivery-time-badge-container {
    flex: 0 0 1.8rem;
  }

  .address-container {
    flex: 1 0;
    padding: 0 0 0 0.5rem;
  }

  .customer-identity-container {
    flex: 1 0;
    padding: 0 0 0 0.5rem;
  }

  .pickup-label {
    font-style: italic;
  }
`

export const KitchenCardOrder: React.FC<TProps> = ({
  card,
  onHandle,
  onHandleRecipe,
  canHandle,
  canHandleRecipe,
}) => {
  const getOrderDestination = () => {
    if (card.sector !== null) {
      return card.sector.name
    }
    if (
      card.deliveryType &&
      card.deliveryType.enum === DeliveryTypeEnum.Pickup
    ) {
      return (
        <div className="pickup-label">
          <Trans>To pickup</Trans>
        </div>
      )
    }
    return formatAddress(card.address)
  }

  return (
    <StyledKitchenCardOrder>
      <div className="order-ticker">
        <div className="delivery-time-badge-container">
          <DeliveryTimeBadge
            deliverAt={card.deliverAt ? new Date(card.deliverAt) : null}
            deliveredAt={card.deliveredAt ? new Date(card.deliveredAt) : null}
            isCancelled={false}
            isDelayed={card.delayedDeliveryTime !== null}
          />
        </div>

        <div className="address-container">{getOrderDestination()}</div>

        <div className="customer-identity-container">
          <CustomerIdentity customer={card.customer} />
          <br />
          {card.customer && card.customer.defaultPhone ? (
            <LinkStyled
              href={`tel:${card.customer.defaultPhone.phone
                .toString()
                .replace('/ /g', '')}`}
            >
              {card.customer.defaultPhone.phone}
            </LinkStyled>
          ) : null}
        </div>
      </div>

      <div className="details">
        <HorizontalSeparator />
        {card.isPriority && <KitchenOrderStarIndicator />}
        <KitchenCardItems
          items={card.items}
          canHandle={canHandleRecipe}
          onHandle={item => onHandleRecipe(card, item)}
        />
        <HorizontalSeparator />
        <KitchenOrderAtSummary
          deliverTime={card.deliverAt}
          deliverType={
            card.deliveryType &&
            card.deliveryType.enum === DeliveryTypeEnum.Pickup
              ? 'PERSONAL'
              : 'DELIVER_UNTIL'
          }
        />
        <NoteLine note={card.note} />
        {canHandle && (
          <CardDock position="BottomRight">
            <KitchenCardButton onClick={() => onHandle(card)} />
          </CardDock>
        )}
      </div>
    </StyledKitchenCardOrder>
  )
}
