import {
  FKitchenOrderBaseItems,
  FKitchenOrderFragment,
  OrderStateCategoryEnum,
  QKitchenOrderListQuery,
  SKitchenOrderListSubscription,
  useQKitchenOrderList,
  useSKitchenOrderList,
} from '../../../graph/generated'
import { useOnConnectionRestored } from '../../../hooks/useOnConnectionRestored'
import { RM } from '../../../tools/ramda'
import {
  TKitchenCardItem,
  TKitchenCardOrder,
  TKitchenDeck,
  TKitchenDeckInput,
} from '../kitchen.types'
import {
  kitchenOrderStateMapping,
  kitchenRecipeStateMapping,
} from './useKitchenDeck'

const requiredCategories = Object.keys(
  kitchenOrderStateMapping,
) as OrderStateCategoryEnum[]

export function useKitchenOrderDeck({
  branch,
  occasions,
  sectors,
}: TKitchenDeckInput) {
  const skip = branch === null

  const variables = {
    branches: [branch!],
    occasions,
    sectors,
  }

  const { getStash, updateQuery, refetch } = useQKitchenOrderList(
    {
      ...variables,
      categories: requiredCategories,
    },
    { skip },
  )

  useOnConnectionRestored(refetch)

  useSKitchenOrderList(
    {
      ...variables,
      // subscription needs to watch for finished state to remove such orders from view
      categories: requiredCategories.concat(OrderStateCategoryEnum.Finished),
    },
    {
      skip,
      onSubscriptionData({ subscriptionData: { data: dataUpdate } }) {
        if (dataUpdate) {
          updateQuery(prev => handleSubscriptionUpdate(dataUpdate, prev))
        }
      },
    },
  )

  return getStash(data => normalizeToKitchenDeck(data.orders))
}

function normalizeToKitchenDeck(orders: RoA<FKitchenOrderFragment>) {
  const cards: TKitchenCardOrder[] = orders.map(mapOrderToKitchenCard)
  return RM.groupBy(o => o.state, cards) as TKitchenDeck
}

function mapOrderToKitchenCard(
  order: FKitchenOrderFragment,
): TKitchenCardOrder {
  return {
    ...order,
    type: 'order',
    state: kitchenOrderStateMapping[order.orderStateCategory]!,
    time: order.deliverAt || order.createdAt,
    items: order.items.map(mapOrderItemToCardItem),
  }
}

export function mapOrderItemToCardItem(
  orderItem: FKitchenOrderBaseItems,
): TKitchenCardItem {
  return {
    id: orderItem.id,
    code: orderItem.recipe.code,
    name: orderItem.recipe.name,
    state: kitchenRecipeStateMapping[orderItem.state.enum]!,
    isReady: kitchenRecipeStateMapping[orderItem.state.enum] === 'done',
    sideDishes: orderItem.sidedishes.map(RM.prop('recipe')),
  }
}

function handleSubscriptionUpdate(
  update: SKitchenOrderListSubscription,
  prev: QKitchenOrderListQuery,
) {
  const { order } = update.updatedOrder
  if (!order || !prev.orders) {
    return prev
  }
  if (!requiredCategories.includes(order.orderStateCategory)) {
    return { orders: prev.orders.filter(o => o.id !== order.id) }
  }
  let orderExists = false
  const orders = prev.orders.map(o => {
    if (o.id === order.id) {
      orderExists = true
      return order
    }
    return o
  })
  if (!orderExists) {
    orders.unshift(order)
  }
  return { orders }
}
