import {
  FKitchenSectorRecipeFragment,
  OrderRecipeStateEnum,
  QKitchenSectorListQuery,
  QKitchenSectorListSectors,
  SKitchenSectorListSubscription,
  useQKitchenSectorList,
  useSKitchenSectorList,
} from '../../../graph/generated'
import { useOnConnectionRestored } from '../../../hooks/useOnConnectionRestored'
import { RM } from '../../../tools/ramda'
import {
  TKitchenCardItem,
  TKitchenCardState,
  TKitchenDeck,
  TKitchenDeckInput,
} from '../kitchen.types'
import { kitchenRecipeStateMapping } from './useKitchenDeck'

const requiredStates = Object.keys(
  kitchenRecipeStateMapping,
) as OrderRecipeStateEnum[]

export function useKitchenSectorDeck({ branch, sectors }: TKitchenDeckInput) {
  const skip = branch === null

  const variables = {
    branch: branch!,
    sectors,
  }

  const { getStash, updateQuery, refetch } = useQKitchenSectorList(
    {
      ...variables,
      states: requiredStates,
    },
    { skip },
  )

  useOnConnectionRestored(refetch)

  useSKitchenSectorList(
    {
      ...variables,
      // subscription needs to watch for finished state to remove such orders from view
      states: requiredStates.concat(
        OrderRecipeStateEnum.Finished,
        OrderRecipeStateEnum.Canceled,
      ),
    },
    {
      skip,
      onSubscriptionData({ subscriptionData: { data: dataUpdate } }) {
        if (dataUpdate) {
          updateQuery(prev => handleSubscriptionUpdate(dataUpdate, prev))
        }
      },
    },
  )

  return getStash(data =>
    normalizeToKitchenDeck(data.sectorRecipes, data.sectors),
  )
}

function normalizeToKitchenDeck(
  sectorRecipes: RoA<FKitchenSectorRecipeFragment>,
  sectors: RoA<QKitchenSectorListSectors>,
) {
  const makeKey = (sectorId: ID, state: TKitchenCardState) =>
    `${sectorId}-${state}`

  const initialState = (RM.mapObjIndexed(
    () => [],
    RM.invertObj(kitchenRecipeStateMapping),
  ) as unknown) as TKitchenDeck

  const groupedBySectorAndState = RM.groupBy(
    ({ sector, state }) =>
      makeKey(sector.id, kitchenRecipeStateMapping[state.enum]!),
    sectorRecipes,
  )

  const sortByTime = RM.sortBy(RM.prop('createdAt'))

  return sectors.reduce((acc, sector) => {
    return RM.mapObjIndexed((cards, state) => {
      const key = makeKey(sector.id, state as TKitchenCardState)
      const relatedSectorRecipes = groupedBySectorAndState[key]
      if (relatedSectorRecipes) {
        const sortedRecipes = sortByTime(relatedSectorRecipes)
        const items = sortedRecipes.map(
          (sectorRecipe): TKitchenCardItem => ({
            id: sectorRecipe.id,
            code: sectorRecipe.recipe.code,
            name: sectorRecipe.recipe.name,
            state: kitchenRecipeStateMapping[sectorRecipe.state.enum]!,
            isReady: [
              OrderRecipeStateEnum.Ready,
              OrderRecipeStateEnum.Cooked,
            ].includes(sectorRecipe.state.enum),
            sideDishes: sectorRecipe.sideDishes.map(RM.prop('recipe')),
          }),
        )
        return cards.concat({
          id: sector.id,
          type: 'sector',
          name: sector.name,
          time: sortedRecipes[0].createdAt,
          state: state as TKitchenCardState,
          isPriority: false,
          items,
        })
      }
      return cards
    }, acc) as TKitchenDeck
  }, initialState)
}

function handleSubscriptionUpdate(
  update: SKitchenSectorListSubscription,
  prev: QKitchenSectorListQuery,
): QKitchenSectorListQuery {
  const { sectorRecipe } = update.updatedSectorRecipe
  if (!sectorRecipe || !prev.sectorRecipes) {
    return prev
  }
  if (!requiredStates.includes(sectorRecipe.state.enum)) {
    return {
      ...prev,
      sectorRecipes: prev.sectorRecipes.filter(s => s.id !== sectorRecipe.id),
    }
  }
  let exists = false
  const sectorRecipes = prev.sectorRecipes.map(s => {
    if (s.id === sectorRecipe.id) {
      exists = true
      return sectorRecipe
    }
    return s
  })
  if (!exists) {
    sectorRecipes.unshift(sectorRecipe)
  }
  return { ...prev, sectorRecipes }
}
