import React, { Fragment } from 'react'

import { TKitchenCardItem } from '../kitchen.types'
import { KitchenCardItem } from './KitchenCardItem'

interface IProps {
  items: RoA<TKitchenCardItem>
  canHandle: boolean
  onHandle(item: TKitchenCardItem): Promise<void>
}

export const KitchenCardItems: React.FC<IProps> = ({
  items,
  canHandle,
  onHandle,
}) => {
  return (
    <div>
      {items.map(item => (
        <Fragment key={item.id}>
          <KitchenCardItem
            item={item}
            isChecked={item.isReady}
            canHandle={canHandle}
            onHandle={() => onHandle(item)}
          />
          <>
            {item.sideDishes.map((sideDish, idx) => (
              <KitchenCardItem
                key={`${item.id}-${sideDish.id}`}
                item={sideDish}
                isFirstSidedish={idx === 0 && canHandle}
              />
            ))}
          </>
        </Fragment>
      ))}
    </div>
  )
}
