import styled from 'styled-components/macro'

interface IProps {
  position: 'TopRight' | 'BottomRight' | 'BottomLeft' | 'TopLeft'
}

export const CardDock = styled.div<IProps>`
  position: absolute;
  ${({ position }) =>
    position === 'BottomRight' || position === 'BottomLeft'
      ? 'bottom: 0'
      : 'top: 0'};
  ${({ position }) =>
    position === 'BottomLeft' || position === 'TopLeft'
      ? 'left: 0'
      : 'right: 0'};
`
