import { Trans } from '@lingui/macro'
import Color from 'color'
import React from 'react'
import { Element } from 'react-scroll'
import styled from 'styled-components'

import {
  DeliveryTypeEnum,
  FOrderTickerFragment,
} from '../../../graph/generated'
import { formatAddress } from '../../../helpers/formats'
import { Colors, Radiuses } from '../../../styling'
import { CustomerIdentity } from '../atoms/CustomerIdentity'
import { DeliveryTimeBadge } from '../atoms/DeliveryTimeBadge'
import { OrderStarIndicator } from '../atoms/OrderStarIndicator'

interface IOrderTickerProps {
  order: FOrderTickerFragment
  showBranch: boolean
  isCancelled: boolean
  onClick(): void
}

const OrderTickerStyled = styled(Element)`
  text-align: left;
  cursor: pointer;
  &:hover {
    background-color: ${Color(Colors.turquoise)
      .alpha(0.05)
      .string()};
  }
  &:active {
    background-color: ${Color(Colors.turquoise)
      .alpha(0.2)
      .string()};
  }
`

const OrderTickerMainContentStyled = styled.div`
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  padding: 0.4rem;
`

const DeliveryTimeBadgeContainerStyled = styled.div`
  flex: 0 0 2rem;
`
const AddressDisplayContainerStyled = styled.div`
  flex: 1 0;
  padding: 0 0 0 0.5rem;
`
const CustomerIdentityContainerStyled = styled.div`
  flex: 1 0;
  padding: 0 0 0 0.5rem;
`
const PriceContainerStyled = styled.div`
  flex: 0 0 3.5rem;
  text-align: right;
  position: relative;
`
const PaidMarkStyled = styled.div`
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;

  &:after {
    content: '';
    position: absolute;
    top: 0.1rem;
    right: -0.3rem;
    left: 0.9rem;
    bottom: 0.3rem;
    border-top: 2px solid ${Colors.orange};
    transform: rotate(13deg);
    transform-origin: 0% 0%;
    opacity: 0.7;
  }
`
const PaidTextStyled = styled.div`
  color: ${Colors.orange};
`

export const ToBranchContainerStyled = styled.div`
  padding: 0.3rem 0.6rem;
  display: inline-block;
  border-top-left-radius: ${Radiuses.Card};
  background-color: ${Colors.green};
  color: ${Colors.white};
`

const ToPickupLabel = styled.div`
  font-style: italic;
  color: ${Colors.violet};
`

export const OrderTicker: React.FC<IOrderTickerProps> = ({
  order,
  isCancelled,
  showBranch,
  onClick,
}) => {
  const getOrderDestination = () => {
    if (order.sector !== null) {
      return order.sector.name
    }
    if (
      order.deliveryType &&
      order.deliveryType.enum === DeliveryTypeEnum.Pickup
    ) {
      return (
        <ToPickupLabel>
          <Trans>To pickup</Trans>
        </ToPickupLabel>
      )
    }
    if (order.address.original) return order.address.original
    return formatAddress(order.address)
  }

  return (
    <OrderTickerStyled onClick={onClick} name={`order-${order.id}`}>
      {showBranch && (
        <ToBranchContainerStyled>
          {order.companyBranch.name}
        </ToBranchContainerStyled>
      )}

      <OrderTickerMainContentStyled>
        <DeliveryTimeBadgeContainerStyled>
          <DeliveryTimeBadge
            deliverAt={order.deliverAt ? new Date(order.deliverAt) : null}
            deliveredAt={order.deliveredAt ? new Date(order.deliveredAt) : null}
            isCancelled={isCancelled}
            isDelayed={order.delayedDeliveryTime !== null}
          />
        </DeliveryTimeBadgeContainerStyled>

        <AddressDisplayContainerStyled>
          {getOrderDestination()}
        </AddressDisplayContainerStyled>

        <CustomerIdentityContainerStyled>
          <CustomerIdentity customer={order.customer} />
        </CustomerIdentityContainerStyled>

        <PriceContainerStyled>
          {order.isPriority && <OrderStarIndicator />}
          {order.totalSum.formattedValue}
          {order.isPaidWithCard && (
            <>
              <PaidMarkStyled data-testid="PaidMark" />
              <PaidTextStyled>
                <Trans>Paid</Trans>
              </PaidTextStyled>
            </>
          )}
        </PriceContainerStyled>
      </OrderTickerMainContentStyled>
    </OrderTickerStyled>
  )
}
