import { Popover } from '@material-ui/core'
import { Close } from '@material-ui/icons'
import { useXFormCtx } from '@speedlo/xform'
import { useLocalStore, useObserver } from 'mobx-react-lite'
import React from 'react'
import styled from 'styled-components/macro'

import { Card } from '../../../atoms/Card'
import { Icon } from '../../../atoms/Icon'
import { Sizer } from '../../../atoms/Sizer'
import { SizerButton } from '../../../atoms/SizerButton'
import { VerticalGap } from '../../../atoms/VerticalGap'
import { OccasionsFilter } from '../../../components/filters/OccasionsFilter'
import { SectorsFilter } from '../../../components/filters/SectorsFilter'
import { FilterDisable } from '../../../components/icons/FilterDisable'
import { FilterEnable } from '../../../components/icons/FilterEnable'
import { i18n } from '../../../i18n'
import { TouchButton } from '../../../molecules/TouchButton'
import { Colors, Spacing } from '../../../styling'
import { TKitchenFormFilter } from '../kitchen.types'

const StyledPopup = styled(Popover)`
  .MuiPopover-paper {
    width: 30rem;
    overflow-x: initial;
    overflow-y: initial;
  }
  .close-button {
    margin: -0.9rem -0.7rem 0rem auto;
    padding: 0.7rem;
    display: block;
    width: 3rem;
    text-align: right;
  }
`

export const KitchenFilters = React.memo<NoChildren>(() => {
  const xform = useXFormCtx<TKitchenFormFilter>()

  const buttonRef = React.useRef<HTMLButtonElement | null>(null)

  const store = useLocalStore(() => ({
    isOpen: false,
    setOpen() {
      this.isOpen = true
    },
    setClosed() {
      this.isOpen = false
    },
    sectorsAvailable: false,
    markSectorsAvailable(available: boolean) {
      this.sectorsAvailable = available
    },
    occassionsAvailable: false,
    markOccasionsAvailable(available: boolean) {
      this.occassionsAvailable = available
    },
    get isEnabled() {
      return this.sectorsAvailable || this.occassionsAvailable
    },
    get isActive() {
      const { values, wasSubmitted } = xform.state
      return (
        wasSubmitted && (values.occasions.length > 0 || values.sectors.length)
      )
    },
  }))

  return useObserver(() => (
    <>
      <SizerButton>
        <TouchButton
          color={store.isActive ? Colors.green : Colors.greyMiddle}
          icon={store.isActive ? FilterDisable : FilterEnable}
          ref={buttonRef}
          iconSize={2}
          onClick={store.setOpen}
          disabled={!store.isEnabled}
        ></TouchButton>
      </SizerButton>
      <StyledPopup
        open={store.isOpen}
        keepMounted={true}
        onClose={(ev, reason) => {
          if (reason === 'backdropClick') {
            xform.api.submitForm()
          }
          store.setClosed()
        }}
        anchorEl={buttonRef.current}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'center',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'center',
        }}
      >
        <Card padding={1}>
          <Icon
            icon={Close}
            color={Colors.greyMiddle}
            fontSize={1}
            className="close-button"
            onClick={() => {
              xform.api.submitForm()
              store.setClosed()
            }}
          />
          <SectorsFilter markAsAvailable={store.markSectorsAvailable} />
          <VerticalGap />
          <OccasionsFilter markAsAvailable={store.markOccasionsAvailable} />
          <VerticalGap />
          <Sizer width={9} height={Spacing.ButtonHeight}>
            <TouchButton
              text={i18n.t`Filter`}
              color={Colors.green}
              onClick={() => {
                xform.api.submitForm()
                store.setClosed()
              }}
            />
          </Sizer>
        </Card>
      </StyledPopup>
    </>
  ))
})
