import { Note } from '@material-ui/icons'
import React from 'react'
import styled from 'styled-components/macro'

import { HorizontalSeparator } from '../../../atoms/HorizontalSeparator'
import { IconLine } from '../../../atoms/IconLine'
import { i18n } from '../../../i18n'
import { Colors } from '../../../styling'

interface INoteLineProps {
  note?: string | null
}

const StyledCurrentNote = styled.span`
  color: ${Colors.orange};
`

export const NoteLine: React.FC<INoteLineProps> = ({ note }) =>
  note ? (
    <>
      <HorizontalSeparator />
      <IconLine icon={Note} labelMargin="0">
        <StyledCurrentNote>
          {note || i18n.t`Without note`.toLocaleLowerCase()}
        </StyledCurrentNote>
      </IconLine>
    </>
  ) : null
