import { toJS } from 'mobx'
import React from 'react'

import { useBranchFilter } from '../../../components/filters/BranchFilter'
import { TKitchenDeckInput, TKitchenFormFilter } from '../kitchen.types'

export function useKitchenFilterForm() {
  const branch = useBranchFilter(false)

  const [filters, setFilters] = React.useState<TKitchenFormFilter>({
    occasions: [],
    sectors: [],
  })

  const onFilterSubmit = React.useCallback((values: TKitchenFormFilter) => {
    setFilters(toJS(values))
  }, [])

  const filterInput = React.useMemo(
    () => ({
      ...filters,
      branch,
    }),
    [branch, filters],
  )

  return [filterInput, onFilterSubmit] as [
    TKitchenDeckInput,
    typeof onFilterSubmit,
  ]
}
