import { css, cx } from 'emotion'
import React from 'react'
import styled from 'styled-components/macro'

import { Colors } from '../styling'

export enum ETimeBadgeVariant {
  UNKNOWN,
  CRITICAL,
  WARNING,
  NORMAL,
}

interface ITimeBadgeProps {
  variant: ETimeBadgeVariant
  minutes?: number
  children?: React.ReactNode
  className?: string
  onClick?(): void
}

const StyledTimeBadge = styled.div`
  height: 2rem;
  line-height: 2rem;
  width: 2rem;
  border-radius: 50%;
  overflow: hidden;
  border: 1px solid white;
  color: ${Colors.white};
  background: ${Colors.greyMiddle};
  text-align: center;
  font-family: 'Open Sans', sans-serif;
`

const variants = {
  [ETimeBadgeVariant.UNKNOWN]: styled(StyledTimeBadge)``,
  [ETimeBadgeVariant.CRITICAL]: styled(StyledTimeBadge)`
    background: ${Colors.red};
  `,
  [ETimeBadgeVariant.WARNING]: styled(StyledTimeBadge)`
    background: ${Colors.orange};
    color: ${Colors.black};
    font-weight: bold;
  `,
  [ETimeBadgeVariant.NORMAL]: styled(StyledTimeBadge)`
    background: ${Colors.green};
  `,
}

const bigNumberStyle = css`
  font-size: 0.8rem;
`
const dashStyle = css`
  font-size: 1.7rem;
  line-height: 1.6rem;
  font-weight: bold;
`
const clickable = css`
  cursor: pointer;
`

export const TimeBadge = React.forwardRef<HTMLDivElement, ITimeBadgeProps>(
  ({ minutes, className, children, variant, onClick }, ref) => {
    const Variant = variants[variant]
    let output = children
    const extraClassNames = ['animated']
    if (minutes !== undefined) {
      if (minutes > 99 || minutes < -9) {
        extraClassNames.push(bigNumberStyle)
      }
      if (minutes < -99) {
        output = '‒'
        extraClassNames.push(dashStyle)
      } else {
        output = minutes.toString()
      }
    }
    if (onClick) {
      extraClassNames.push(clickable)
    }
    return (
      <Variant
        ref={ref}
        key={minutes}
        onClick={onClick}
        className={cx(
          className,
          {
            heartBeat: variant === ETimeBadgeVariant.CRITICAL && minutes! > 0,
            pulse: variant === ETimeBadgeVariant.WARNING,
          },
          ...extraClassNames,
        )}
      >
        {output}
      </Variant>
    )
  },
)
