import {
  OrderRecipeStateEnum,
  OrderStateCategoryEnum,
} from '../../../graph/generated'
import { RM } from '../../../tools/ramda'
import { TKitchenCardState, TKitchenDeck } from '../kitchen.types'

export const kitchenRecipeStateMapping: Partial<
  Record<OrderRecipeStateEnum, TKitchenCardState>
> = {
  [OrderRecipeStateEnum.Fresh]: 'todo',
  [OrderRecipeStateEnum.Preparing]: 'progress',
  [OrderRecipeStateEnum.Ready]: 'progress',
  [OrderRecipeStateEnum.Cooked]: 'done',
}

export const kitchenOrderStateMapping: Partial<
  Record<OrderStateCategoryEnum, TKitchenCardState>
> = {
  [OrderStateCategoryEnum.NewOrders]: 'todo',
  [OrderStateCategoryEnum.Preparing]: 'progress',
  [OrderStateCategoryEnum.ReadyToDeliver]: 'done',
}

export function useKitchenDeck() {
  const initialState = (RM.mapObjIndexed(
    () => [],
    RM.invertObj(kitchenRecipeStateMapping),
  ) as unknown) as TKitchenDeck

  return { initialState }
}
