import React from 'react'
import { Route, Switch } from 'react-router-dom'

import { NotFoundPage } from '../../../pages/NotFoundPage'
import { SecureRoute } from '../../../routes/SecureRoute'
import { ROUTE_KITCHEN, ROUTE_KITCHEN_COMPLETION } from '../kitchen.routes'
import { KitchenCompletionPage } from '../pages/KitchenCompletionPage'
import { KitchenOverviewPage } from '../pages/KitchenOverviewPage'

interface IProps {}

export const KitchenRouting: React.FC<IProps> = () => {
  return (
    <Switch>
      <SecureRoute exact path={ROUTE_KITCHEN} component={KitchenOverviewPage} />
      <SecureRoute
        path={ROUTE_KITCHEN_COMPLETION}
        component={KitchenCompletionPage}
      />
      <Route component={NotFoundPage} />
    </Switch>
  )
}

export default KitchenRouting
