import { Trans } from '@lingui/macro'
import { observer } from 'mobx-react-lite'
import React from 'react'

import { Mobile } from '../../../app/Responsive'
import { CardListTitle } from '../../../atoms/CardListTitle'
import { useRoot } from '../../../atoms/RootProvider'
import { useBranchFilter } from '../../../components/filters/BranchFilter'
import {
  FKitchenCompletionOrderFragment,
  OrderStateCategoryEnum,
} from '../../../graph/generated'
import { OrderNotificationSound } from '../../../molecules/OrderNotificationSound'
import { RM } from '../../../tools/ramda'
import { KitchenInfoMessage } from '../atoms/KitchenInfoMessage'
import { kitchenOrderStateMapping } from '../hooks/useKitchenDeck'
import { mapOrderItemToCardItem } from '../hooks/useKitchenOrderDeck'
import { TKitchenCardCompletion } from '../kitchen.types'
import { KitchenCardCompletion } from '../molecules/KitchenCardCompletion'
import { useKitchenCompletion } from './KitchenCompletionLoader'

type TProps = NoChildren & {
  category: OrderStateCategoryEnum
  noOrdersMessage: ReactNode
  mobileHeadline: ReactNode
}

export const KitchenCompletionShelf = observer<TProps>(
  ({ category, noOrdersMessage, mobileHeadline }) => {
    const { order: orderModel } = useRoot()
    const branch = useBranchFilter(false)
    const orders = useKitchenCompletion()

    if (!branch) {
      return (
        <KitchenInfoMessage>
          <Trans>Select a branch</Trans>
        </KitchenInfoMessage>
      )
    }

    if (orders === null) {
      return (
        <KitchenInfoMessage>
          <Trans>Loading</Trans>
        </KitchenInfoMessage>
      )
    }

    const cards = orders
      .filter(RM.propEq('orderStateCategory', category))
      .map(mapOrderToKitchenCard)

    if (cards.length === 0) {
      return <KitchenInfoMessage>{noOrdersMessage}</KitchenInfoMessage>
    }

    const shouldPlayNotification = category === OrderStateCategoryEnum.NewOrders

    return (
      <>
        {mobileHeadline && (
          <Mobile>
            <CardListTitle>{mobileHeadline}</CardListTitle>
          </Mobile>
        )}
        <div data-testid={`order-list-${category}`}>
          {cards.map(card => (
            <KitchenCardCompletion
              key={card.id}
              card={card}
              showBranch={orderModel.list.branchesFilter.length > 1}
            />
          ))}
        </div>
        <OrderNotificationSound shouldPlay={shouldPlayNotification} playNow />
      </>
    )
  },
)

function mapOrderToKitchenCard(
  order: FKitchenCompletionOrderFragment,
): TKitchenCardCompletion {
  return {
    ...order,
    type: 'completion',
    state: kitchenOrderStateMapping[order.orderStateCategory]!,
    time: order.deliverAt || order.createdAt,
    items: order.items.map(mapOrderItemToCardItem),
  }
}
