import React from 'react'
import styled from 'styled-components/macro'

import { CardDock } from '../../../atoms/CardDock'
import { HorizontalSeparator } from '../../../atoms/HorizontalSeparator'
import { Colors, FontSizes, Radiuses, Spacing } from '../../../styling'
import { KitchenCardButton } from '../atoms/KitchenCardButton'
import { TKitchenCardProps, TKitchenCardSector } from '../kitchen.types'
import { KitchenCardItems } from './KitchenCardItems'

type TProps = NoChildren &
  TKitchenCardProps<TKitchenCardSector> & {
    card: TKitchenCardSector
  }

const StyledKitchenCardSector = styled.div`
  font-size: ${FontSizes.CompactCard};
  background-color: ${Colors.white};
  color: ${Colors.greyDarkest};
  border-radius: ${Radiuses.Card};
  margin-bottom: ${Spacing.Middle};
  position: relative;

  .details {
    padding: 0 0.2rem 0.1rem 0.2rem;
    position: relative;
  }

  .sector-name {
    padding: 0.3rem 0 0.3rem 2.5rem;
    font-size: 1.1rem;
  }
`

export const KitchenCardSector: React.FC<TProps> = ({
  card,
  canHandle,
  canHandleRecipe,
  onHandle,
  onHandleRecipe,
}) => {
  return (
    <StyledKitchenCardSector>
      <div className="sector-name">{card.name}</div>

      <div className="details">
        <HorizontalSeparator />
        <KitchenCardItems
          items={card.items}
          canHandle={canHandleRecipe}
          onHandle={item => onHandleRecipe(card, item)}
        />
        {canHandle && (
          <>
            <HorizontalSeparator />
            <CardDock position="BottomRight">
              <KitchenCardButton
                color={card.state === 'todo' ? Colors.green : Colors.pink}
                onClick={() => onHandle(card)}
              />
            </CardDock>
          </>
        )}
      </div>
    </StyledKitchenCardSector>
  )
}
