import React from 'react'

import {
  OrderRecipeStateEnum,
  OrderStateEnum,
  useMKitchenOrderHandle,
  useMKitchenOrderRecipeHandle,
  useMKitchenSectorHandle,
  useMKitchenSectorRecipeHandle,
} from '../../../graph/generated'
import { useCanMutate } from '../../../hooks/useAccessGuard'
import { RM } from '../../../tools/ramda'
import { TKitchenCard, TKitchenCardItem } from '../kitchen.types'

export function useKitchenHandler() {
  const canHandleOrder = useCanMutate('addOrderState')
  const canHandleOrderRecipe = useCanMutate('updateOrderRecipeState')
  const canHandleSector = useCanMutate('updateSectorRecipes')
  const canHandleSectorRecipe = useCanMutate('updateSectorRecipe')

  const [handleOrder] = useMKitchenOrderHandle()
  const [handleOrderRecipe] = useMKitchenOrderRecipeHandle()
  const [handleSector] = useMKitchenSectorHandle()
  const [handleSectorRecipe] = useMKitchenSectorRecipeHandle()

  const canHandleCard = (card: TKitchenCard) => {
    if (card.state === 'todo') {
      if (card.type === 'order' || card.type === 'completion') {
        return canHandleOrder
      }
      if (card.type === 'sector') {
        return canHandleSector
      }
    }
    // ! temporary, should be removed when cashbox can finish those recipes
    if (card.type === 'sector' && card.state === 'done') {
      return canHandleSector
    }
    if (card.type === 'completion' && card.state === 'done') {
      return canHandleOrder
    }
    return false
  }

  const canHandleCardItem = (card: TKitchenCard) => {
    if (card.state !== 'todo') {
      if (card.type === 'order' || card.type === 'completion') {
        return canHandleOrderRecipe
      }
      if (card.type === 'sector') {
        return canHandleSectorRecipe
      }
    }
    return false
  }

  const handleCard = React.useCallback(
    async (card: TKitchenCard) => {
      if (card.type === 'order') {
        await handleOrder({ orderId: card.id, state: OrderStateEnum.InProduce })
      }
      if (card.type === 'sector') {
        if (card.state === 'todo') {
          await handleSector({
            sectorId: card.id,
            state: OrderRecipeStateEnum.Preparing,
          })
        }
        if (card.state === 'done') {
          await handleSector({
            sectorId: card.id,
            state: OrderRecipeStateEnum.Finished,
          })
        }
      }
      if (card.type === 'completion') {
        if (card.state === 'todo') {
          await handleOrder({
            orderId: card.id,
            state: OrderStateEnum.Accepted,
          })
        }
        if (card.state === 'done') {
          await handleOrder({
            orderId: card.id,
            state: OrderStateEnum.Finished,
          })
        }
      }
    },
    [handleOrder, handleSector],
  )

  const handleCardItem = React.useCallback(
    async (card: TKitchenCard, item: TKitchenCardItem) => {
      const nextItemState = getItemToggledState(card, item)
      if (card.type === 'order' || card.type === 'completion') {
        await handleOrderRecipe({
          orderRecipeId: item.id,
          state: nextItemState,
        })
      }
      if (card.type === 'sector') {
        await handleSectorRecipe({
          sectorRecipeId: item.id,
          state: nextItemState,
        })

        // special case to move a whole sector to done when all items are ready
        // need to mark the one that was moved just now so the following condition can pass
        item.isReady = nextItemState === OrderRecipeStateEnum.Ready
        if (card.items.every(RM.propEq('isReady', true))) {
          await handleSector({
            sectorId: card.id,
            state: OrderRecipeStateEnum.Cooked,
          })
        }
      }
    },
    [handleOrderRecipe, handleSector, handleSectorRecipe],
  )

  return {
    canHandleCard,
    canHandleCardItem,
    handleCard,
    handleCardItem,
  }
}

function getItemToggledState(
  card: TKitchenCard,
  item: TKitchenCardItem,
): OrderRecipeStateEnum {
  switch (item.state) {
    case 'todo':
    case 'progress': {
      if (card.type === 'sector') {
        return item.isReady
          ? OrderRecipeStateEnum.Preparing
          : OrderRecipeStateEnum.Ready
      }
      return OrderRecipeStateEnum.Cooked
    }
    case 'done': {
      return OrderRecipeStateEnum.Preparing
    }
  }
}
