import React from 'react'

import { FCustomerFragment } from '../../../graph/generated'

interface ICustomerNameProps {
  customer: FCustomerFragment | null
}

export const CustomerIdentity: React.FC<ICustomerNameProps> = ({ customer }) =>
  customer ? (
    <span>{`${
      customer.lastName
        ? `${customer.firstName} ${customer.lastName}`
        : customer.defaultPhone
        ? customer.defaultPhone.phone
        : ``
    }`}</span>
  ) : (
    <i>unknown</i>
  )
