import { CancelOutlined } from '@material-ui/icons'
import { MINUTE, useTimer } from '@speedlo/hooks'
import { differenceInMinutes } from 'date-fns'
import { css } from 'emotion'
import React from 'react'
import styled from 'styled-components/macro'

import { ETimeBadgeVariant, TimeBadge } from '../../../atoms/TimeBadge'
import { Colors } from '../../../styling'

interface IDeliveryTimeBadgeProps {
  deliverAt: Maybe<Date>
  deliveredAt: Maybe<Date>
  isCancelled?: boolean
  isDelayed?: boolean
  onClick?(): void
}

const cancelIconPadding = css`
  padding: 0.2rem 0.3rem 0.1rem 0.3rem;
`
const StyledDelayedTimeMark = styled.div`
  background: ${Colors.white};
  border: 2px solid ${Colors.greyMiddle};
  width: 0.6rem;
  height: 0.6rem;
  border-radius: 0.7rem;
  position: absolute;
`

export const DeliveryTimeBadge: React.FC<IDeliveryTimeBadgeProps> = ({
  deliverAt,
  deliveredAt,
  isCancelled = false,
  isDelayed = false,
  onClick,
}) => {
  const timerEnabled = !(deliveredAt || isCancelled)
  const now = useTimer({ interval: MINUTE, enabled: timerEnabled })

  let variant = ETimeBadgeVariant.UNKNOWN

  if (isCancelled) {
    return (
      <TimeBadge variant={variant} className={cancelIconPadding}>
        <CancelOutlined />
      </TimeBadge>
    )
  }

  if (!deliverAt) {
    return (
      <TimeBadge variant={variant}>
        <span role="img" aria-label="Unknown">
          ❔
        </span>
      </TimeBadge>
    )
  }

  const minutes = differenceInMinutes(deliverAt, deliveredAt || now)

  if (minutes > 20) {
    variant = ETimeBadgeVariant.NORMAL
  } else if (minutes > 0) {
    variant = ETimeBadgeVariant.WARNING
  } else {
    variant = ETimeBadgeVariant.CRITICAL
  }

  return (
    <>
      {isDelayed && <StyledDelayedTimeMark />}
      <TimeBadge minutes={minutes} variant={variant} onClick={onClick} />
    </>
  )
}
