import { XForm } from '@speedlo/xform'
import { observer } from 'mobx-react-lite'
import React from 'react'

import { QKitchenCompletionOrderListOrders } from '../../../graph/generated'
import { useKitchenCompletionDeck } from '../hooks/useKitchenCompletionDeck'
import { useKitchenFilterForm } from '../hooks/useKitchenFilterForm'

type TOrder = QKitchenCompletionOrderListOrders

const loaderContext = React.createContext<RoA<TOrder> | null>(null)

export const KitchenCompletionLoader: React.FC = observer(props => {
  const [filters, onFilterSubmit] = useKitchenFilterForm()

  const deck = useKitchenCompletionDeck(filters)

  return (
    <XForm initialValues={filters} onSubmit={onFilterSubmit}>
      <loaderContext.Provider value={deck}>
        {props.children}
      </loaderContext.Provider>
    </XForm>
  )
})

export function useKitchenCompletion() {
  return React.useContext(loaderContext)
}
